/**
 * Feature flags for the Lead Command Center
 * This is the main entry point for the feature flags module
 */

// Re-export components
export { FeatureGuard } from './components';

// Available feature IDs - matches the original enum from types/index.ts for compatibility
export enum FeatureID {
  // Core functionality
  WORKFLOW_TRIGGER = 'workflow_trigger',
  STATUS_MONITORING = 'status_monitoring',
  WEBHOOK_INTEGRATION = 'webhook_integration',
  
  // Optional features
  WORKFLOW_PARAMETERIZATION = 'workflow_parameterization',
  
  // User Management
  USER_MANAGEMENT = 'user_management',
  
  // Future features
  ADVANCED_DASHBOARD = 'advanced_dashboard',
  ANALYTICS_REPORTING = 'analytics_reporting',
  CAMPAIGN_MANAGEMENT = 'campaign_management',
  RESULTS_VISUALIZATION = 'results_visualization',
  MULTI_ACCOUNT = 'multi_account'
}

// Feature flag service interface
export interface FeatureFlagsService {
  enableFeature: (featureId: FeatureID) => void;
  disableFeature: (featureId: FeatureID) => void;
  isFeatureEnabled: (featureId: FeatureID) => boolean;
}

// Implementation of the feature flags service
class FeatureFlagsServiceImpl implements FeatureFlagsService {
  private enabledFeatures: Set<FeatureID> = new Set();

  enableFeature(featureId: FeatureID): void {
    this.enabledFeatures.add(featureId);
    console.log(`Feature enabled: ${featureId}`);
  }

  disableFeature(featureId: FeatureID): void {
    this.enabledFeatures.delete(featureId);
    console.log(`Feature disabled: ${featureId}`);
  }

  isFeatureEnabled(featureId: FeatureID): boolean {
    return this.enabledFeatures.has(featureId);
  }
}

// Export singleton instance
export const featureFlagsService = new FeatureFlagsServiceImpl();

// For backward compatibility with existing imports
export default featureFlagsService; 